import React, { Component } from 'react';
import { Mutation } from 'react-apollo';
import gql from 'graphql-tag';
import Form from './styles/Form';
import formatMoney from '../lib/formatMoney';

class CreateItem extends Component {
	state = {
		title:'',
		description:'',
		image:'',
		largeImage:'',
	}

	//if we  use this arrow property, there is no need to bind handleChange to the cirrect this,  it wil be handled
	handleChange = (e) => {
		const { name, type, value } = e.target;
		const val = type === 'number'? parseFloat(value) : value;
		//we can let the state change field dynanically by using a placeholder in side [ ] (see JS's computed property name)
		this.setState({[name]:val});

		console.log (this.state.title);
	}
	render() {
		return (
			<Form>
				<fieldset>
					<label htmlFor="title">
						Title
						<input 
							type="text" 
							id="title" 
							name="title"
							required
							value ={this.state.title}
							onChange={this.handleChange}
						/>
					</label>

					<label htmlFor="price">
						Title
						<input 
							type="number" 
							id="price" 
							name="price"
							required
							value ={this.state.price}
							onChange={this.handleChange}
						/>
					</label>
				</fieldset>
			</Form>
		);
	}
};

export default CreateItem;